@import url(//fonts.googleapis.com/css?family=Overpass:300,400,600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.bg_pair_heading{
  background-color: #f5f9fc52;
  position: absolute;
  z-index: 200;
  color: #fff;
}
.reset_jds{
  display: -webkit-flex;
  display: flex ;
  padding-left: 20px;
  padding-right: 20px;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-size: 20px;
}
.btn_drop{
  background-color: transparent;
  border-color: transparent;
  color: #fff;
}
.modal_custom.modal-content{
  background-color: #08254A;
}
.clain_btn{
  color:#fff;
  font-size: 22px;
  border: 2px solid #1b1b97;
  padding: 10px 28px;
  border-radius: 28px;
}
.claimtxt1{
  font-size: 80px;
  font-weight: 700;
}
.claimtxt2{
  font-size: 90px;
  font-weight: 700;
}
.claimtxt3{
  font-size: 80px;
  font-weight: 700;
}
.claimtxt4{
  font-size: 30px;
}
.dropdown-menu{
  background-color: rgb(8, 37, 74) !important;
}
.dropdown-item{
  color: #fff!important;
}
.dropdown-item:hover{
  color: #000!important;
}
.marque{
  color: #fff;
  background-color: #08254a96;
}

.coming_card{
  height: 400px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: #08254A;;
  margin-top: 30px;
  margin-bottom: 30px;
  border-radius: 12px;
}
.blog_card{
  background-color: #08254A;;
  margin-top: 30px;
  margin-bottom: 30px;
  border-radius: 12px;
}
.coming_card h1{
  color: #fff;
  font-size: 30px;
}

